/* Preview */

.preview {
  grid-column: 1 / -1;
  grid-row: 1 / -1;
  position: relative;

  @media (max-width: $mobile-width-only) {
    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      max-height: 302px;
      z-index: -1;

      background-image: url("../img/preview-images/cat-bg.jpg");
      background-repeat: no-repeat;
      background-position: top center;
      background-size: cover;

      @media (min-resolution: 2dppx) {
        background-image: url("../img/preview-images/cat-bg@2x.jpg");
      }
    }

    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      max-height: 302px;
      z-index: -1;
      background-color: rgba($basic-green, 0.85);
    }
  }
}

.preview__wrapper {
  display: flex;
  flex-direction: column;

  @media (max-width: $mobile-width-only) {
    align-items: center;
  }

  @media (min-width: $tablet-width) {
    background-image: none;
  }

  @media (min-width: $desktop-width) {
    display: grid;
    grid-template-columns: 400px auto;
    grid-template-rows: 200px 1fr;
    position: relative;
  }
}

.page-title {
  @media (max-width: $mobile-width-only) {
    text-align: center;
  }

  @media (min-width: $desktop-width) {
    grid-column-start: 1;
    grid-column-end: 3;
    grid-row-start: 2;
    grid-row-end: 3;
  }
}

.preview__subtitle {
  @include oswald-uppercase(14px, 14px, 400);
  color: $white;

  @media (max-width: $mobile-width-only) {
    text-align: center;
    margin-top: 11px;
  }

  @media (min-width: $tablet-width) {
    color: $black;
    font-size: 20px;
    line-height: 20px;
    margin-bottom: 49px;
    margin-left: 88px;
    z-index: 2;
    order: -1;
  }

  @media (min-width: $desktop-width) {
    grid-column-start: 1;
    grid-column-end: 2;
    grid-row-start: 3;
    grid-row-end: 4;
    margin-top: 18px;
    margin-bottom: 52px;
    margin-left: 81px;
  }
}

.preview__image {
  @media (max-width: $mobile-width-only) {
    margin-top: 14px;
    width: 280px;
    height: 270px;
  }

  @media (min-width: $tablet-width) {
    order: 2;
    width: 709px;
    height: 609px;
    margin-top: -88px;
    z-index: -1;
    margin-left: 0;
  }

  @media (min-width: $desktop-width) {
    width: 552px;
    height: 499px;
    grid-column-start: 2;
    grid-column-end: -1;
    grid-row-start: 1;
    grid-row-end: -1;
    margin-left: 34px;

    position: absolute;
    top: 229px;
    left: 13px;
    z-index: -1;
  }
}
